.searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  width: 100%;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.buttonSearch {
  padding: 8px;
  color: rgb(39, 37, 37);
  font-size: 18px;
  background-color: #9dabfc;
  border: 1px solid #0b1653;
}

.inputSearch {
  padding: 6px;
  color: rgb(39, 37, 37);
  font-size: 18px;
}
